.container{margin: 0;
    width: 1589px;
    height: 500px;    
    left:77px;
    background-color: #001929;
    margin: 15px ;
    margin-left: 90px;
    border-radius: 17px;
    
}
h4{
    color:#fff;
    font-size: 30px;
    font-weight: 700;
    margin: 140px;
    margin-left: 20px;
    padding: 40px;
    margin-top: 200px;
}
.wrapper{
    display: flex;
    justify-content: center;
    margin: 33px;
    
    
}
.box{
    width:300px;
    height:170px;
    top:2008px;
    left:748px;
    display: flex;
    justify-content: space-between;
    margin: 2%;
    margin-top: 3px;
    
    padding-inline: 55px;
    background-color: rgba(25, 25, 112, 0.308);
    border-radius: 10px;
    color:#fff;
}

.box-content{
    display: block;
    margin: 10px;
    padding: 12px;
}
.font{
    font-size: 30px;
    font-weight: bold;
    
    color:#fff;
}
.box-content h1{
    font-weight: bold;
    font-size: 30px;
}
@media (max-width: 768px) {
    .container{
        margin-left: 2px;
        margin-right: 2px;
        display: flex;
        width: 99%;
        height: 800px;
        

    }
    h4{
        font-size: 24px ;
        
        height: 85%;
        text-align: center;
        margin-top: 20px;
        padding: 4px;
        margin: 3px;
        

    }
    .wrapper {
        flex-direction: column;
        margin-left: 12px; 
        margin-top: -29px;
    }

    .box {
        height:150px;
        margin:5px ; 
        margin-top: 4px;
        margin-bottom: 8px;
        padding-inline: 0;
    }

}


  











