.textcontainer {
    width: 100%;
    height: 500px;
    background: rgba(0, 25, 41, 1);   
    padding-left: 20px;
    padding:154px;
    
}


h5 {
   
    color: #fff;
    text-align: center;
    font-family: Inter;
    font-size: 50px;
    font-weight: 800;   
    letter-spacing: 0em;
    text-align: center;
}
.contenttext{
    color:#fff;
    text-align: center;
}
.submitbtn{
    width: 660px;
    height: 76px;
    display: flex;
    margin-inline: 10px;
    margin: 10px;
   margin-inline: 200px;
   text-align: center;
   padding-left: 200px;



}
.btn1{
    background-color: rgb(0, 140, 255);
    padding:14px;
    border-radius: 15px;
    font-weight: bold;
    color:#fff;
    margin-bottom: 26px;
}
.btn2{
    margin-top: 20px;
    margin-inline: 30px;
    color: rgb(0, 140, 255);
    font-weight: bold;
}
@media (max-width: 768px) {
    .textcontainer {
        padding: 0 10px;
        height: 500px;
        padding-top: 100px;
        padding-bottom: 2px;
    }

    h5 {
        text-align: center;
        
        font-size: 33px; /* Reduce font size for smaller screens */
    }
    .contenttext{
        margin-top: 25px;
        font-size: 15px;
    }
    .card-text{
        margin-inline: 20px;
        font-size: 15px;
    }

    .submitbtn {
        width: 80%;
        padding:5px;
        display:inline-block;
        align-items: center;
        margin-inline: 20px; /* Make the button full-width */
    }
}
